<template>
  <div style="display: flex">
    <div class="intro-text" style="min-height:calc(100vh - 60px);width: 155px;background-color: #ecedf1;flex-shrink: 0;">
      <div style="font-size: 18px;font-weight: bold;text-align: center;margin: 10px 0 10px 0;color:grey">设备信息</div>
      <el-menu :default-openeds="['info']" :collapse="isCollapse" :collapse-transition="false" router background-color="#ecedf1" text-color="#222221"
               active-text-color="#fff" style="border: none" :default-active="$route.path">
        <el-menu-item index="/deviceList" >
          <i class="el-icon-caret-right"></i>
          <span slot="title">设备列表</span>
        </el-menu-item>
        <el-menu-item index="/childDevice" >
          <i class="el-icon-caret-right"></i>
          <span slot="title">部件表</span>
        </el-menu-item>
        <el-menu-item index="/meterList" >
          <i class="el-icon-caret-right"></i>
          <span slot="title">仪表列表</span>
        </el-menu-item>
        <div style="font-size: 18px;font-weight: bold;text-align: center;margin: 10px 0 10px 0;color:grey">点检标准录入</div>
        <el-menu-item index="/entryForm" >
          <i class="el-icon-caret-right"></i>
          <span slot="title">标准录入</span>
        </el-menu-item>
        <el-menu-item index="/standardView" >
          <el-dropdown placement="bottom">
            <div>
              <i class="el-icon-location-outline"></i>
              <span slot="title">设备标准</span>
            </div>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item @click.native="$router.push('/standardView')">运行工标准</el-dropdown-item>
              <el-dropdown-item @click.native="$router.push('/standardView2')">维修工标准</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </el-menu-item>
        <el-menu-item index="/meterList1" >
          <el-dropdown placement="bottom">
            <div>
              <i class="el-icon-location-outline"></i>
              <span slot="title">仪表标准</span>
            </div>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item @click.native="$router.push('/meterList1')">运行工标准</el-dropdown-item>
              <el-dropdown-item @click.native="$router.push('/meterList2')">维修工标准</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </el-menu-item>
      </el-menu>
      <div style="padding-top: 40px">
        <img src="../../assets/金色时代.png" height="234" width="153"/>
      </div>
    </div>
    <div  style="padding: 20px 0 0 20px;width: 100%">
      <!--  任务查看页面-->
      <div>
        <el-input style="width: 300px;margin-right: 10px" placeholder="请输入搜索内容" v-model="searchText"></el-input>
        <el-button type="primary" @click="load(1)">查询</el-button>
        <el-button type="info" @click="reset">重置</el-button>
      </div>
      <div style="margin: 10px 0">
        <el-button type="primary" plain @click="handleAdd">新增</el-button>
        <el-button type="danger" plain @click="delBatch" v-if="user.authority==='管理员' || user.authority==='超级管理员'">批量删除</el-button>
        <!--        <el-button type="info" plain  @click="exportData">批量导出</el-button>-->
      </div>
      <el-table :data="tableData" stripe :header-cell-style="{backgroundColor: 'aliceblue', color:'#666'}" @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="序号" prop="id" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="设备编号" prop="equipmentNum" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="设备名称" prop="equipmentName" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="所属部门" prop="department" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="录入时间" prop="time"  align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="设备类型" prop="type" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="设备状态" prop="state" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column prop="equipmentImg" label="图片" >
          <template v-slot="scope">
            <div style="display: flex">
              <el-image style="width: 40px; height: 40px" v-if="scope.row.equipmentImg"
                        :src="scope.row.equipmentImg" :preview-src-list="[scope.row.equipmentImg]"></el-image>
            </div>
          </template>
        </el-table-column>
        <el-table-column label="设备基本信息" align="center" :show-overflow-tooltip="true">
          <template v-slot="scope">
            <el-button size="mini" type="primary" plain @click="redirectToBasicInfo(scope.row)">查看</el-button>
          </template>
        </el-table-column>
        <el-table-column label="操作" align="center" width="180" fixed="right">
          <template v-slot="scope">
            <el-button size="mini" type="primary" plain @click="handleEdit(scope.row)" v-if="user.authority==='管理员' || user.authority==='超级管理员'">编辑</el-button>
            <el-button size="mini" type="danger" plain  @click="del(scope.row.id)" v-if="user.authority==='超级管理员'">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div style="margin: 10px 0">
        <el-pagination
            @current-change="handleCurrentChange"
            :current-page="pageNum"
            :page-size="pageSize"
            layout="total, prev, pager, next"
            :total="total">
        </el-pagination>
      </div>
      <!--      新增编辑页面-->
      <el-dialog title="新增设备" :visible.sync="fromVisible" width="30%">
        <el-form :model="form" label-width="80px" style="padding-right: 20px" :rules="rules" ref="formRef">
          <el-form-item label="设备编号" prop="equipment_num"><el-input v-model="form.equipmentNum" placeholder="设备编号"></el-input></el-form-item>
          <el-form-item label="设备名称" prop="equipment_name"><el-input v-model="form.equipmentName" placeholder="设备名称"></el-input></el-form-item>
          <el-form-item label="所属部门" prop="department"><el-input v-model="form.department" placeholder="所属部门" style="width: 300px"></el-input></el-form-item>
          <el-form-item label="设备类型" prop="type"><el-input v-model="form.type" placeholder="设备类型"></el-input></el-form-item>
          <el-form-item label="图片">
            <el-upload
                class="avatar-uploader"
                :action="$baseUrl + '/file/upload'"
                :file-list="form.equipmentImg? [form.equipmentImg] : []"
                list-type="picture"
                :on-success="handleImgSuccess"
            >
              <el-button type="primary">上传图片</el-button>
            </el-upload>
          </el-form-item>



        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="fromVisible = false">取 消</el-button>
          <el-button type="primary" @click="save">确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  name: "DeviceList",
  data(){
    return{
      equipmentNum:'',
      equipmentName:'',
      packageId:'',
      department: '',
      type:'',
      searchText: '',
      tableData:[],
      pageNum: 1,
      pageSize: 10,
      total: 0,
      fromVisible: false,
      form: {
        equipmentImg: '',
      },
      fileList: [], // 上传文件列表
      user: JSON.parse(localStorage.getItem('honey-user') || '{}'),
      rules: {
        equipmentNum: [
          { required: true, message: '请输入设备编号', trigger: 'blur' },
        ]
      },
      ids: []
    }
  },
  created() {
    this.load()
  },
  methods: {

    redirectToBasicInfo(row) {
      // 使用 $router.push() 方法将参数作为查询参数传递给目标页面
      this.$router.push({ path: '/basicInfo', query: { id: row.id, equipmentNum: row.equipment_num }});
    },

    exportData(){   //批量导出数据
      if (!this.ids.length){  //没有选择行的时候，全部导出，或者根据我的搜索条件导出
        window.open(this.$baseUrl + '/deviceList/export?token=' + this.user.token + "&equipmentName=" + this.equipmentName + "&type=" + this.type+ "&department=" + this.department)
      } else {   //[1,2,3] => '1,2,3'
        let idStr = this.ids.join(',')
        window.open(this.$baseUrl + '/deviceList/export?token=' + this.user.token + '&ids=' + idStr)
      }
    },
    delBatch() {
      if (!this.ids.length) {
        this.$message.warning('请选择数据')
        return
      }
      this.$confirm('您确认批量删除这些数据吗？', '确认删除', {type: "warning"}).then(response => {
        this.$request.delete('/deviceList/delete/batch', { data: this.ids }).then(res => {
          if (res.code === '200') {   // 表示操作成功
            this.$message.success('操作成功')
            this.load(1)
          } else {
            this.$message.error(res.msg)  // 弹出错误的信息
          }
        })
      }).catch(() => {})
    },
    handleSelectionChange(rows) {   // 当前选中的所有的行数据
      this.ids = rows.map(v => v.id)
    },
    del(id) {
      this.$confirm('您确认删除吗？', '确认删除', {type: "warning"}).then(response => {
        this.$request.delete('/deviceList/delete/' + id).then(res => {
          if (res.code === '200') {   // 表示操作成功
            this.$message.success('操作成功')
            this.load(1)
          } else {
            this.$message.error(res.msg)  // 弹出错误的信息
          }
        })
      }).catch(() => {})
    },
    // handleEdit(row) {
    //   this.form = {};  // 清空表单对象
    //   this.form.equipmentImg = '';  // 清空图片
    //   this.form = JSON.parse(JSON.stringify(row));  // 深拷贝数据到form对象
    //   this.fromVisible = true;  // 打开弹窗
    // },
    handleEdit(row) {
      this.form = JSON.parse(JSON.stringify(row));  // 深拷贝数据到form对象
      this.form.equipmentImg = '';  // 清空图片
      this.fromVisible = true;  // 打开弹窗
    },
    handleAdd() {   // 新增数据
      this.form={};
      console.log("form is "+this.form);
      this.fromVisible = true   // 打开弹窗
    },
    save() {   // 保存按钮触发的逻辑  它会触发新增或者更新
      this.$refs.formRef.validate((valid) => {
        if (valid) {
          this.$request({
            url: this.form.id ? '/deviceList/update': '/deviceList/add',
            method: this.form.id ? 'PUT' : 'POST',
            data: this.form
          }).then(res => {
            if (res.code === '200') {  // 表示成功保存
              this.$message.success('保存成功')
              this.load(1)
              this.fromVisible = false
            } else {
              this.$message.error(res.msg)  // 弹出错误的信息
            }
          })
        }
      })

    },

    reset() {
      this.searchText = '';
      this.load(1);
    },
    load(pageNum) {  // 分页查询
      if (pageNum)  this.pageNum = pageNum
      this.$request.get('/deviceList/selectByPage', {
        params: {
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          equipmentName: this.equipmentName,
          type:this.type,
          department:this.department,
          searchText: this.searchText // 直接传递 searchText 参数
        }
      }).then(res => {
        this.tableData = res.data.records
        this.total = res.data.total
      })
    },
    handleCurrentChange(pageNum) {
      this.load(pageNum)
    },
    // handleImgSuccess(response, file, fileList) {
    //   // 把user的头像属性换成上传的图片的链接
    //   this.form.equipmentImg = response.data
    // },
    handleImgSuccess(response, file, fileList) {
      this.form.equipmentImg = response.data;
      console.log("img is"+response.data);
      this.fileList = fileList; // 更新文件列表
    },
  }
}
</script>
<style scoped>
.intro-text {
  word-break: break-all;
  word-wrap: break-word;
}
</style>